const renderMultilineText = (ctx, text, x, y, maxWidth, lineHeight) => {
	const words = text.split(' ')

	const line = words.reduce((acc, item, idx) => {
		const tempLine = acc + item + ' '
		const metrics = ctx.measureText(tempLine)
		const tempWidth = metrics.width

		if (tempWidth > maxWidth && idx > 0) {
			ctx.fillText(acc, x, y)
			acc = item + ' '
			y += lineHeight
		} else {
			acc = tempLine
		}

		return acc
	}, '')

	ctx.fillText(line, x, y)
}
const createImage = (files, callback) => {
	if (!files.length) {
		return
	}

	const image = new Image()

	image.onload = () => callback(image)
	image.src = URL.createObjectURL(files[0])


}


