	renderCanvasContent = () => {
		const ctx = this.canvas.getContext('2d')
		ctx.fillStyle = "white"
		ctx.fillRect (0, 0, this.canvas.width, this.canvas.height)
		ctx.font = `${this.state.styleText} ${this.properties.fontSize}px ${this.properties.fontCanvas}`
		ctx.textBaseline = 'top'

		if (this.state.background) {
			this.drawBackgroundImage()
		}

		if (this.state.image) {
			this.drawFrontImage(this.state.image)
		}

		this.renderHeader(ctx);
		this.renderText(ctx)
	}

	renderText = (ctx) => {
		ctx.fillStyle = this.properties.textColor

		const maxWidth =
			this.canvas.width -
			this.properties.padding * 2 -
			this.properties.textRightPadding -
			this.properties.textLeftPadding

		const x = this.properties.padding + this.properties.textLeftPadding
		let y = this.properties.padding + this.properties.textTopPadding

		if (this.state.header) {
			y += this.properties.headerFontSize * 2 + this.properties.headerTopPadding
		}

		renderMultilineText(ctx, this.state.text, x, y, maxWidth, this.properties.fontSize)
	}
	renderHeader = (ctx) => {
		ctx.fillStyle = this.properties.textColor

		const maxWidth =
			this.canvas.width -
			this.properties.padding * 2 -
			this.properties.headerRightPadding -
			this.properties.headerLeftPadding

		const x = this.properties.padding + this.properties.headerLeftPadding
		const y = this.properties.padding + this.properties.headerTopPadding

		renderMultilineText(ctx, this.state.header, x, y, maxWidth, this.properties.fontSize)
	}

	drawBackgroundImage = () => {
		const { background } = this.state
		const ctx = this.canvas.getContext('2d')

		ctx.drawImage(background, 0, 0, this.canvas.width, this.canvas.height)
	}

	drawFrontImage = () => {
		const { image } = this.state
		const padding = this.properties.padding * 2
		const ctx = this.canvas.getContext('2d')

		ctx.drawImage(
			image,
			this.properties.padding,
			this.properties.padding,
			this.canvas.width - padding,
			this.canvas.height - padding
		)
	}

