	handleDownload = () => {
		const base64 = this.canvas.toDataURL('image/png')
		const filename = `${new Date().getTime()}.png`

		this.setState({ popout: <ScreenSpinner /> })
		setTimeout(() => { this.setState({ popout: null, activePanel: "Home" }) }, 3000);

		bridge
			.send('VKWebAppGetClientVersion')
			.then(data => {
				const checkVersion = data.platform
				if (checkVersion == "ios") {
					setTimeout(() => { this.setState({activePanel: "Home" }) }, 3000);				
					bridge.send("VKWebAppDownloadFile", { "url": base64, "filename": filename });
				} else {

					const link = document.createElement('a')
					link.href = base64
					link.download = filename
					link.style.display = 'none'

					document.body.appendChild(link)
					link.click()
					link.parentNode.removeChild(link)
				}
			})
			.catch(error => {
				this.setState({ activePanel: "Home" })
			});
	}
