	//Глобальный сброс
	resetGlobal() {
		this.setState({
			backAlert: "Выберите файл",
			frontAlert: "Выберите файл",
			headerFontSizeForSlider: 120,
			headerLeftPaddingForSlider: 1000,
			headerRightPaddingForSlider: 0,
			headerTopPaddingForSlider: 0,
			paddingSliderForSlider: 120,
			fontSizeForSlider: 110,
			textLeftPaddingForSlider: 0,
			textRightPaddingForSlider: 0,
			textTopPaddingForSlider: 0,
			colorSel: "#4169e1",
			heightSel: 3508,
			widthSel: 2481,
			fontSel: "Brushfont",
			heightFormat: 3508,
			widthFormat: 2481,
			shouldSizeFront: "Для качественного отображения фона листа, картинка должна быть больше 2481x3508",
			shouldSizeBack: "Для качественного отображения заднего фона, картинка должна быть больше 2605х3683",
			styleText: "normal",
			inputText: "Включить наклон",
			image: null,
			background: null,
		}, () => this.renderCanvasContent());
	}

	handleTextChange() {
		const result = document.getElementById('textInput').value;
		this.state.result = document.getElementById('textInput').value;

		if (result.length > 0) {
			this.setState({ textStatus: "valid" }, () => this.renderCanvasContent());
		}
		else {
			this.setState({ textStatus: "error" });
		}
	}

	showResult() {

		const result = document.getElementById('textInput').value;

		this.properties.fontCanvas = this.state.fontSel
		this.properties.textColor = this.state.colorSel

		this.properties.height = this.state.heightSel
		this.properties.width = this.state.widthSel

		this.properties.headerFontSize = this.state.headerFontSizeForSlider
		this.properties.headerLeftPadding = this.state.headerLeftPaddingForSlider
		this.properties.headerRightPadding = this.state.headerRightPaddingForSlider
		this.properties.headerTopPadding = this.state.headerTopPaddingForSlider

		this.properties.padding = this.state.paddingSliderForSlider
		this.properties.fontSize = this.state.fontSizeForSlider
		this.properties.textLeftPadding = this.state.textLeftPaddingForSlider
		this.properties.textRightPadding = this.state.textRightPaddingForSlider
		this.properties.textTopPadding = this.state.textTopPaddingForSlider

		if (result.length > 0) {
			this.state.textHeader = document.getElementById('textHeader').value;
			this.state.result = document.getElementById('textInput').value;
			this.setState({ activePanel: "Result", popout: <ScreenSpinner /> }, () => this.goToPage());
			setTimeout(() => { this.setState({ popout: null, text: this.state.result, header: this.state.textHeader }, () => this.renderCanvasContent()) }, 1500);

		} else {
			this.setState({ textStatus: "error" });
		}

	}


